/* Main container side-by-side */
.main-wrapper {
  max-width: 90%;
  margin: auto;
  background: white;
  margin-top: 5px;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);

  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: 40px;
  padding: 25px;
  background: rgb(229 230 231);
  min-height: 100vh;
  color: white;
}
/* #0f172a; */
/* LEFT SIDE */
.left-section {
  width: 40%;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.logo {
  width: 81%;
  margin-top: 80px;
  margin-left: 20px;
}

.left-text {
  max-width: 81%;
  margin-top: 40px;
  margin-left: 20px;
}

.contact-title {
  margin-left: 34%;
}

.title {
  font-size: 36px;
  font-weight: bold;
  font-family: math;
  color: rgb(222, 221, 23);
  margin-left: 20px;
}

.subtitle {
  font-size: 30px;
  opacity: 0.8;
  color: gray;
}

/* VERTICAL LINE */
.divider {
  width: 2px;
  background: rgba(255, 255, 255, 0.2);
  height: 600px;
}

/* RIGHT SIDE FORM */
.right-section {
  width: 40%;
  color: black;
  background-color: #f1df97;
  padding: 2%;
  border-radius: 15px;
}

.dropdown {
  color: black;
}

.contact-title {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

input,
select,
textarea {
  padding: 12px;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.07);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
}

textarea {
  min-height: 100px;
}

button {
  margin-top: 10px;
  padding: 12px;
  background: #06b6d4;
  color: #0f172a;
  border: none;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
}

/* MOBILE RESPONSIVE */
@media (max-width: 900px) {
  .main-wrapper {
    flex-direction: column;
    padding: 25px;
  }

  .divider {
    display: none;
  }

  .left-section,
  .right-section {
    width: 100%;
  }
}
